import React from 'react';
import {connect} from 'react-redux';
import {Link} from 'react-router-dom';
import {fetchStreams} from '../../actions';
import "../../styles/Global.css";

class StreamList extends React.Component{
  componentDidMount() {
      this.props.fetchStreams()
  }

  renderAdmin(stream) {
    if(stream.userId === this.props.currentUserId) {
      return (
        <div className="right floated content">
         <Link to={`/streams/edit/${stream.id}`} className="ui button primary">Editar</Link>
          <Link to={`/streams/delete/${stream.id}`} className="ui button negative">
            Deletar
          </Link>
        </div>
      )
    }
  }
  renderList() {
    return this.props.streams.map(stream => {
      return (
        <div className="item" key={stream.id}>
           {this.renderAdmin(stream)}
          <i className="large middle aligned"/>
          <div className="content">
            <Link to={`/streams/${stream.id}`} className="header">
            {stream.title}
            </Link>
            <img src={stream.img}/>
            <div className="description">
              <p>{stream.description1}</p>
              <p>{stream.description2}</p>
              <p>{stream.description3}</p>
              <p>{stream.description4}</p>
              <p>{stream.description5}</p>
            </div>

            
          </div>
         
        </div>
      )
    })
  }

  renderCreate() {
    if(this.props.isSignedIn) {
      return (
        <div style={{textAlign: 'right'}}>
          <Link to="/streams/new" className="ui button primary" >
            Criar um Componente
          </Link>
        </div>
      )

    }
  }
  render() {
    return (
      <div>
        <h2>Midia Kit Livelo - Widgets</h2>
        <div className="ui celled list">{this.renderList()}</div>
        {this.renderCreate()}
      </div>
    )
  }

}

const mapStateToProps = (state) => {
  return {streams: Object.values(state.streams),
          currentUserId: state.auth.userId,
        isSignedIn: state.auth.isSignedIn}
}  
export default connect(mapStateToProps, {fetchStreams}) (StreamList)